import { Component, OnInit, Input } from '@angular/core';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-confm-device-form',
  templateUrl: './confm-device-form.component.html',
  styleUrls: ['./confm-device-form.component.scss']
})
export class ConfmDeviceFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  list = {
    address: {
      multiple: false,
      title: 'Address',
      title_sidebar: 'Address List',
      search: true,
      apiurl: 'api/obj/address/list/',
      addNew: 'address'
    },
    credential: {
      multiple: false,
      title: 'Credential (Optional)',
      title_sidebar: 'Credentials List',
      search: true,
      apiurl: 'api/confmanager/credentials/list/',
    }
  }


  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  setAddress(data){
    this.options.data.address = data
  }

  setCreden(data){
    this.options.data.credential = data
  }

}
