import { Component, OnInit, Input } from '@angular/core';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-confm-credential-form',
  templateUrl: './confm-credential-form.component.html',
  styleUrls: ['./confm-credential-form.component.scss']
})
export class ConfmCredentialFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

}
