import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Pipe({
  name: 'cmPreview'
})
export class CmPreviewPipe implements PipeTransform {

  start = false;

  constructor(private _sanitizer: DomSanitizer) { }

  transform(value: any, args?: any): SafeHtml {

    let re = new RegExp("^___.*");
    let reST = new RegExp("(^START_ .*|^___START_ .*)");
    let reEN = new RegExp(".* END_$");

    if ( reST.test( value ) ) { this.start = true; }

    if ( this.start ) {
      if ( reEN.test( value ) ) { this.start = false; value.replace(/END_$/, ''); }
      if ( re.test( value ) ) {
        value = '<span class="bg-danger">' + value.replace(/^___/, '').replace(/^START_\s|\sEND_$/, '') + "</span>"
      } else {
        value = '<span>' + value.replace(/^START_\s|\sEND_$/, '') + "</span>"
      }
    }

    return this._sanitizer.bypassSecurityTrustHtml(value);
  }

}
