import { Component, OnInit, Input } from '@angular/core';
//CDK
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
//rxjs
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-confm-model-form',
  templateUrl: './confm-model-form.component.html',
  styleUrls: ['./confm-model-form.component.scss']
})
export class ConfmModelFormComponent implements OnInit {

  @Input() options

  @Input() validation
  validation_ex = { send: null }
  @Input() loading

  expect_line = this.clearEx()
  editMode = new BehaviorSubject(-1)

  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  addExpecration(direction){
    this.validation_ex = { send: null }
    if (this.expect_line.send == ''){
      this.validation_ex.send = ['Send can not be empty'];
      return
    }
    if ( direction == 'down')
      this.options.data.expectations.push(this.makeClone(this.expect_line))
    else
      this.options.data.expectations.unshift(this.makeClone(this.expect_line))
    this.clearEx(true);
  }

  clearEx(trigger = false){
    if (trigger) {
      this.expect_line = this.clearEx()
      return
    }
    return { expect: '', send: '', hidden: 0, write: 1}
  }

  clone(index){
    this.options.data.expectations.splice(index+1, 0, this.makeClone(this.options.data.expectations[index]))
  }
  del(index){
    if ( this.options.data.expectations.length == 1 ) return
    this.options.data.expectations.splice(index, 1)
  }
  edit(index){
    this.expect_line = this.makeClone(this.options.data.expectations[index])
    this.editMode.next(index)
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.options.data.expectations, event.previousIndex, event.currentIndex);
  }

  cancel(){
    this.editMode.next(-1)
    this.clearEx(true);
  }

  save(){
    this.options.data.expectations[this.editMode.getValue()] = this.expect_line;
    this.cancel()
  }

}
