import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/confmanager/';

@Injectable({
  providedIn: 'root'
})
export class PreviewService {

  constructor(private http: HttpClient) {}

  getDir(path) {
    const params = new HttpParams()
    .set('path', path);
    return this.http.get<any>(API_URL + 'dir/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.list
        } )
      ).toPromise()
  }

  getDirExploer(path) {
    const params = new HttpParams()
    .set('path', path);
    return this.http.get<any>(API_URL + 'dir/exploer/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.list
        } )
      )
  }

  addFolder(folder:{}) {
    return this.http.post<any>(API_URL + 'dir/add/', folder)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  delFolder(folder:{}) {
    return this.http.post<any>(API_URL + 'dir/delete/', folder)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  moveFolder(folder:{}) {
    return this.http.post<any>(API_URL + 'dir/mv/', folder)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  commitList(file:{}) {
    const params = new HttpParams()
    .set('extra', JSON.stringify(file));
    return this.http.get<any>(API_URL + 'diff/list/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  previewInit(data:{}) {
    return this.http.post<any>(API_URL + 'diff/brief/', data)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  pleaseGiveMeTacacs(data:{}) {
    return this.http.post<any>(API_URL + 'tacacs/', data)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  pleaseGiveMeTacacsLog(data:{}) {
    return this.http.post<any>(API_URL + 'tacacs/aaa/', data)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

}
