import { Component, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { ITreeOptions, TreeNode} from 'angular-tree-component';
//Service
import { PreviewService } from '../../../../../../pages/confmanager/preview.service'

@Component({
  selector: 'kt-confm-tree-view',
  templateUrl: './confm-tree-view.component.html',
  styleUrls: ['./confm-tree-view.component.scss']
})
export class ConfmTreeViewComponent implements OnInit {

  @ViewChild('tree_view') tree;

  @Output() path = new EventEmitter<string>();

  options: ITreeOptions = {
    getChildren: this.getChildren.bind(this),
  };

  nodes: any[] = [];

  constructor(
    private service: PreviewService,
  ) {
    this.nodes = [
      {
        name: 'root',
        id: '/',
        path: '/',
        partial_path: [''],
        hasChildren: true
      }
    ];
  }

  ngOnInit() {
  }

  getChildren(node: any) {
    return this.service.getDir(node.data.path)
  }

  afterInit() {
    const someNode = this.tree.treeModel.getNodeById('/');
    someNode.expand();

    const firstRoot = this.tree.treeModel.roots[0];
    firstRoot.setActiveAndVisible();
  }

  loadChildren_(e){
    // console.log(e)
    if (!e.node.children.length) {
      // console.log(e.node)
      e.node.data.hasChildren = false;
    }
  }

  select_(e){
    e.node.expand();
    let path = e.node.data.id || '/'
    this.path.emit(path);
  }

}
